  border-top: 2px solid #ececec;
  box-shadow: 0 8px 10px 0 rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  position: fixed;
  top: 20%;
  width: 100%;
  z-index: 200;
  text-align: center; }

.modal-button {
  margin-top: 31px;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
  width: 81.2%;
  padding: 18px 0; }

.modal__label {
  padding: 15px 32px;
  border: 3px solid #ececec;
  line-height: 16px;
  text-transform: uppercase; }
  .modal__label :hover + input {
    background-color: red; }

.modal__title {
  margin: 30px 85px 12px;
  text-transform: uppercase;
  font-size: 25px;
  line-height: 30px; }

.modal__form-controls {
  display: flex;
  justify-content: space-between;
  width: 81.2%;
  margin: 0 auto; }

.modal__form-text {
  display: block;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  font-weight: 400;
  margin: 10px 0 22px 0; }

.modal__form {
  padding-bottom: 46px; }

@media (min-width: 768px) {
  .modal--cart {
    width: 565px;
    left: 25%; }
  .modal-button {
    width: 350px; }
  .modal__label {
    padding: 15px 46px; }
  .modal__form-controls {
    width: 350px; }
  .modal__title {
    font-size: 30px;
    line-height: 57px; }
  .modal__form-text {
    font-size: 17px;
    line-height: 30px; } }

